<template>
  <section class="step-1">
    <div class="add-place-des">
      <h4>Location Details</h4>
    </div>

    <!-- 
    <FormulateInput
      type="text"
      label="text"
      validation="required"
      error-behavior="live"
    /> -->
    <ValidationObserver v-slot="{ invalid }">
      <form @submit.prevent="" class="form">
        <ValidationProvider
          name="userState"
          rules="required"
          v-slot="{ errors }"
        >
        <FormulateInput
        type="text"
        label="text"
        />
          <div class="form-input-wrapper">
            <label for="">State</label>
            <div class="form-input-element">
              <input v-model="userState" type="text" />
            </div>
            <span class="form-error">{{ errors[0] }}</span>
          </div>
        </ValidationProvider>

        <ValidationProvider name="state" rules="required" v-slot="{ errors }">
          <div class="form-input-wrapper">
            <label for="">City</label>
            <div class="form-input-element">
              <input v-model="city" type="text" />
            </div>

            <span class="form-error">{{ errors[0] }}</span>
          </div>
        </ValidationProvider>

        <ValidationProvider name="street" rules="required" v-slot="{ errors }">
          <div class="form-input-wrapper">
            <label for="">Street</label>
            <div class="form-input-element">
              <input v-model="street" type="text" />
            </div>
            <span class="form-error">{{ errors[0] }}</span>
          </div>
        </ValidationProvider>

        <Navigation :valid="!invalid" />
      </form>
    </ValidationObserver>
  </section>
</template>

<script>
import Navigation from "./Navigation.vue";
import { ValidationObserver, ValidationProvider, extend } from "vee-validate";
// import { required, email, alpha } from "vee-validate/dist/rules";

extend("required", {
  validate(value) {
    return {
      required: true,
      valid: ["", " ", null, undefined].indexOf(value) === -1,
      message: "This field is required",
    };
  },
  computesRequired: true,
  message: "This field is required",
});

export default {
  components: { Navigation, ValidationObserver, ValidationProvider, extend },
  data() {
    return {};
  },
  computed: {
    userState: {
      get() {
        return this.$store.state.steps.step1.userState;
      },
      set(value) {
        this.$store.commit("setState", { state: value });
      },
    },
    city: {
      get() {
        return this.$store.state.steps.step1.city;
      },
      set(value) {
        this.$store.commit("setCity", { city: value });
      },
    },
    street: {
      get() {
        return this.$store.state.steps.step1.street;
      },
      set(value) {
        this.$store.commit("setStreet", { street: value });
      },
    },
    progressNow: {
      get() {
        return this.$store.state.progressNow;
      },
      set(value) {
        this.$store.commit("setProgress", { progressNow: value });
      },
    },
    validation: {
      get() {
        return this.$store.state.validation;
      },
      set(value) {
        this.$store.commit("setValidate", { validation: value });
      },
    },
  },
  methods: {},
};
</script>

<style scoped lang="scss">
.formulate-input .formulate-input-element {
  max-width: 100% !important;
  width: 100% !important;
}
</style>
